/**
 * MotorCheckoutContainer.js
 * Checkout Container for motor
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { getFormValues } from 'redux-form';
import PropTypes from 'prop-types';

import { getSessionParams } from '../../actions/petActions';
import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import CheckoutWrapper from '../../components/Templates/Checkout/CheckoutWrapper';
import { gaTag, gaEvent } from '../../lib/tagFunctions';

import {
  compactCheckoutAttributes,
  getCheckoutParamsWording,
  getCheckoutHeaderDataCoverage,
  getCompactSubDetails,
} from '../../lib/helperFunctions';

class MotorCheckoutContainer extends Component {
  constructor(props) {
    super(props);

    const {
      motorForm,
      motorMoreForm,
      motorDetailsForm,
      selectedPlanData,
      isAdHoc,
      adHocData,
    } = this.props;

    const paramsWording = getCheckoutParamsWording(
      isAdHoc,
      adHocData,
      selectedPlanData,
      () => getSessionParams(
        selectedPlanData,
        motorDetailsForm,
        motorForm,
        motorMoreForm,
      ),
    );

    const headerCoverage = getCheckoutHeaderDataCoverage('Motor Insurance', isAdHoc, adHocData, selectedPlanData, paramsWording.params);
    const motorDetailFormToUse = isAdHoc ? adHocData.data.session_params : motorDetailsForm;

    this.state = {
      headerData: headerCoverage.headerData,
      mainDetails: {
        title: 'Details',
        attributes: compactCheckoutAttributes([
          { title: 'Email', description: motorDetailFormToUse.email },
          { title: 'Full Name', description: motorDetailFormToUse.name },
          { title: 'Family Name', description: motorDetailFormToUse.family_name },
          { title: 'Given Name', description: motorDetailFormToUse.given_name },
          { title: 'ID Number', description: motorDetailFormToUse.nric },
          { title: 'Gender', description: motorDetailFormToUse.gender },
          { title: 'Marital Status', description: motorDetailFormToUse.marital_status },
          { title: 'Date of birth', description: motorDetailFormToUse.birth_date },
          { title: 'Mobile Number', description: motorDetailFormToUse.mobile_number },
          { title: 'Address', description: motorDetailFormToUse.address },
          { title: 'Unit no', description: motorDetailFormToUse.unit_no },
          { title: 'Postal', description: motorDetailFormToUse.postal },
          { title: 'Start Date', description: motorDetailFormToUse.start_date },
          { title: 'End Date', description: motorDetailFormToUse.end_date },
          // { title: 'Coverage', description: headerCoverage.coverage },
        ]),
      },
      subDetails: [getCompactSubDetails({
        key: 'Car',
        leftDetail: {
          title: 'Driving License',
          attributes: compactCheckoutAttributes([
            { title: 'Pass date', description: motorDetailFormToUse.pass_date },
            { title: 'Years of experience', description: motorDetailFormToUse.years_of_experience },
            { title: 'No claim discount', description: motorDetailFormToUse.ncd },
            { title: 'Offence free', description: motorDetailFormToUse.offence_free },
            { title: 'No of cliams in the last 3 years', description: motorDetailFormToUse.num_claims },
          ]),
        },
        rightDetail: {
          title: 'Vehicle',
          attributes: compactCheckoutAttributes([
            { title: 'Vehicle no', description: motorDetailFormToUse.vehicle_number },
            { title: 'Vehicle no', description: motorDetailFormToUse.vehicle_no },
            { title: 'Vehicle usage', description: motorDetailFormToUse.vehicle_usage },
            { title: 'Car brand', description: motorDetailFormToUse.vehicle_make },
            { title: 'Car model', description: motorDetailFormToUse.vehicle_model },
            { title: 'No of seats', description: motorDetailFormToUse.num_seats },
            { title: 'Years of registration', description: motorDetailFormToUse.reg_year },
            { title: 'Years of manufacture', description: motorDetailFormToUse.manufacture_year },
            { title: 'Off-peak car', description: motorDetailFormToUse.off_peak },
            { title: 'Commencement date', description: motorDetailFormToUse.commencement_date },
          ]),
        },
      })],
      ...paramsWording,
    };
  }

  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.CHECKOUT.index);

    gaTag({
      page_title: 'Motor - Checkout',
      page_path: '/motor/checkout',
    });
  }

  render() {
    const {
      headerData,
      mainDetails,
      subDetails,
      wording,
      params,
    } = this.state;

    const {
      isLoggedIn,
      charged,
      setFormState,
      history,
      isAdHoc,
      adHocData,
      sessionId,
    } = this.props;

    return (
      <CheckoutWrapper
        headerData={headerData}
        mainDetails={mainDetails}
        subDetails={subDetails}
        sessionId={sessionId}
        isLoggedIn={isLoggedIn}
        charged={charged}
        backToDetails={() => {
          history.goBack();
        }}
        wording={wording}
        isAdHoc={isAdHoc}
        adHocData={adHocData}
        params={params}
        category="motor"
        trackingTags={{
          payment: () => {
            gaEvent('Motor - CheckoutPage_ProceedPayment', {
              event_category: 'Motor',
              event_label: 'Motor - CheckoutPage_ProceedPayment',
            });
          },
          receipt: () => {
            if (!isAdHoc) {
              setFormState(FORMSTATES.SUBMITTED.index);
              history.push(`/motor${FORMSTATES.SUBMITTED.path}`);
            }
            gaTag({
              page_title: 'Motor - Receipt',
              page_path: '/motor/Receipt',
            });
          },
        }}
      />
    );
  }
}

MotorCheckoutContainer.defaultProps = {
  charged: null,
  sessionId: null,
  adHocData: null,
  isAdHoc: false,
  isLoggedIn: false,
  motorForm: null,
  motorMoreForm: null,
  motorDetailsForm: null,
  selectedPlanData: null,
};

MotorCheckoutContainer.propTypes = {
  charged: PropTypes.bool,
  sessionId: PropTypes.string,
  isLoggedIn: PropTypes.bool,
  motorForm: PropTypes.object,
  motorMoreForm: PropTypes.object,
  motorDetailsForm: PropTypes.object,
  selectedPlanData: PropTypes.object,
  adHocData: PropTypes.object,
  isAdHoc: PropTypes.bool,
  setFormState: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  const sessionId = ownProps.isAdHoc ? ownProps.sessionId : state.insurance.sessionId;
  return ({
    motorForm: getFormValues('motor')(state),
    motorMoreForm: getFormValues('motor-detail')(state),
    motorDetailsForm: getFormValues('motor-detail')(state),
    selectedPlanData: state.pet.selectedPlanData,
    sessionId,
  });
};

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MotorCheckoutContainer));
