/**
 * PACheckoutContainer.js
 * Checkout Container for PA
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { getFormValues } from 'redux-form';
import PropTypes from 'prop-types';

import { getSessionParams } from '../../actions/paActions';
import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import CheckoutWrapper from '../../components/Templates/Checkout/CheckoutWrapper';
import { gaTag, gaEvent, ampTag } from '../../lib/tagFunctions';

import {
  compactCheckoutAttributes,
  getCheckoutParamsWording,
  getCheckoutHeaderDataCoverage,
  getCompactSubDetails,
} from '../../lib/helperFunctions';

class PACheckoutContainer extends Component {
  constructor(props) {
    super(props);

    const {
      paForm,
      paDetailsForm,
      selectedPlanData,
      isAdHoc,
      adHocData,
    } = this.props;

    const paramsWording = getCheckoutParamsWording(
      isAdHoc,
      adHocData,
      selectedPlanData,
      () => getSessionParams(paForm, paDetailsForm, selectedPlanData),
    );

    const headerCoverage = getCheckoutHeaderDataCoverage('Personal Accident Insurance', isAdHoc, adHocData, selectedPlanData, paramsWording.params);
    const sessionParams = isAdHoc ? adHocData.data.session_params : paramsWording.params.sessionParams.session_params;
    const isSpouse = sessionParams.num_adult === '2';
    let SpouseAttribute = [];
    let familyMemberText = isAdHoc ? null : '1 Adult';
    if (isSpouse) {
      SpouseAttribute = [
        { title: 'My spouse\'s occupation', description: sessionParams.family[0].occupation },
      ];
      familyMemberText = '2 Adults';
    }

    const numChildren = isAdHoc ? null : '0';

    this.state = {
      headerData: headerCoverage.headerData,
      mainDetails: {
        title: 'Insurance',
        attributes: compactCheckoutAttributes([
          { title: 'Plan type', description: sessionParams.plan_type },
          { title: 'Family members', description: sessionParams.family_members || familyMemberText },
          { title: 'Number of children', description: sessionParams.num_children || numChildren },
          { title: 'My occupation', description: sessionParams.occupation },
          ...SpouseAttribute,
          { title: 'Start Date', description: sessionParams.start_date },
          { title: 'End Date', description: sessionParams.end_date },
          // { title: 'Coverage', description: headerCoverage.coverage },
        ]),
      },
      subDetails: this.getSubDetails(paramsWording.params.sessionParams),
      ...paramsWording,
    };
  }

  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.CHECKOUT.index);

    gaTag({
      page_title: 'PersonalAccident - Checkout',
      page_path: '/personal-accident/checkout',
    });
    ampTag('PAReviewS');
  }

  getSubDetails(sessionParams) {
    const isMyself = sessionParams.session_params.plan_type === 'Myself';

    const myself = getCompactSubDetails({
      key: 'Myself',
      leftDetail: {
        title: 'Details',
        attributes: compactCheckoutAttributes([
          { title: 'Email', description: sessionParams.session_params.email },
          { title: 'Family Name', description: sessionParams.session_params.family_name },
          { title: 'Given Name', description: sessionParams.session_params.given_name },
          { title: 'Full Name', description: sessionParams.session_params.full_name },
          { title: 'Nationality', description: sessionParams.session_params.nationality },
          { title: 'ID type', description: sessionParams.session_params.id_type },
          { title: 'ID number', description: sessionParams.session_params.nric },
          { title: 'Race', description: sessionParams.session_params.race },
          { title: 'Gender', description: sessionParams.session_params.gender },
          { title: 'Date of birth', description: sessionParams.session_params.birth_date },
          { title: 'Marital Status', description: sessionParams.session_params.marital_status },
          { title: 'Address', description: sessionParams.session_params.address },
          { title: 'Unit no', description: sessionParams.session_params.unit_no },
          { title: 'Postal', description: sessionParams.session_params.postal },
          { title: 'Mobile number', description: sessionParams.session_params.mobile_number },
        ]),
      },
      rightDetail: {
        title: 'Work Details',
        attributes: compactCheckoutAttributes([
          { title: 'Occupation', description: sessionParams.session_params.occupation },
          { title: 'Industry', description: sessionParams.session_params.industry },
        ]),
      },
    });

    const familyData = [];
    if (!isMyself && sessionParams.session_params.family) {
      let childCount = 1;
      for (let i = 0; i < sessionParams.session_params.family.length; ++i) {
        const isSpouse = i === 0 && sessionParams.session_params.num_adult === '2';
        const data = sessionParams.session_params.family[i];

        let rightDetail = null;
        if (isSpouse) {
          childCount = 0;
          rightDetail = {
            title: 'Work Details',
            attributes: compactCheckoutAttributes([
              { title: 'Occupation', description: data.occupation },
              { title: 'Industry', description: data.industry },
            ]),
          };
        } else {
          rightDetail = {
            title: 'Education Details',
            attributes: compactCheckoutAttributes([
              { title: 'Full school education', description: data.full_school_education },
            ]),
          };
        }

        const key = isSpouse ? 'Spouse' : `Children ${childCount + i}`;

        familyData.push(getCompactSubDetails({
          key,
          leftDetail: {
            title: 'Details',
            attributes: compactCheckoutAttributes([
              { title: 'Full Name', description: data.full_name },
              { title: 'Nationality', description: data.nationality },
              { title: 'ID type', description: data.id_type },
              { title: 'ID number', description: data.nric },
              { title: 'Relationship', description: data.relationship },
              { title: 'Gender', description: data.gender },
              { title: 'Date of birth', description: data.birth_date },
            ]),
          },
          rightDetail,
        }));
      }
    }

    return [myself, ...familyData];
  }

  render() {
    const {
      headerData,
      mainDetails,
      subDetails,
      wording,
      params,
    } = this.state;

    const {
      isLoggedIn,
      charged,
      history,
      isAdHoc,
      adHocData,
      sessionId,
      setFormState,
    } = this.props;

    return (
      <CheckoutWrapper
        headerData={headerData}
        mainDetails={mainDetails}
        subDetails={subDetails}
        sessionId={sessionId}
        isLoggedIn={isLoggedIn}
        charged={charged}
        backToDetails={() => {
          history.goBack();
        }}
        wording={wording}
        isAdHoc={isAdHoc}
        adHocData={adHocData}
        params={params}
        category="pa"
        trackingTags={{
          payment: (data) => {
            gaEvent('PersonalAccident - CheckoutPage_ProceedPayment', {
              event_category: 'PersonalAccident',
              event_label: 'PersonalAccident - CheckoutPage_ProceedPayment',
            });
            ampTag('PAProceedPayA', {
              cashback: data.rewardsID !== null,
              promo: data.promoCode !== null && data.promoCode !== '',
            });
          },
          receipt: () => {
            if (!isAdHoc) {
              setFormState(FORMSTATES.SUBMITTED.index);
              history.push(`/personal-accident${FORMSTATES.SUBMITTED.path}`);
            }
            gaTag({
              page_title: 'PersonalAccident - Receipt',
              page_path: '/personal-accident/Receipt',
            });
            ampTag('PAConfirmationS');
          },
        }}
      />
    );
  }
}

PACheckoutContainer.defaultProps = {
  charged: null,
  sessionId: null,
  adHocData: null,
  isAdHoc: false,
  paForm: null,
  paDetailsForm: null,
  selectedPlanData: null,
  isLoggedIn: false,
};

PACheckoutContainer.propTypes = {
  charged: PropTypes.bool,
  sessionId: PropTypes.string,
  isLoggedIn: PropTypes.bool,
  paForm: PropTypes.object,
  paDetailsForm: PropTypes.object,
  selectedPlanData: PropTypes.object,
  adHocData: PropTypes.object,
  isAdHoc: PropTypes.bool,
  history: PropTypes.object.isRequired,
  setFormState: PropTypes.func.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  const sessionId = ownProps.isAdHoc ? ownProps.sessionId : state.insurance.sessionId;

  return ({
    paForm: getFormValues('pa')(state),
    paDetailsForm: getFormValues('pa-detail')(state),
    selectedPlanData: state.pa.selectedPlanData,
    sessionId,
  });
};

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PACheckoutContainer));
