/**
 * AdHocOthersContainer.js
 * The generic Checkout container for adhoc
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import CheckoutWrapper from '../../components/Templates/Checkout/CheckoutWrapper';
import { gaTag, gaEvent } from '../../lib/tagFunctions';

import {
  compactCheckoutAttributes,
  getCheckoutParamsWording,
  getCheckoutHeaderDataCoverage,
} from '../../lib/helperFunctions';

const defaultTopNames = [
  'start_date',
  'end_date',
  'vehicle_no',
];

const defaultBottomNames = [
  'email',
  'family_name',
  'given_name',
  'nric',
  'birth_date',
  'mobile_number',
  'address',
  'unit_no',
  'postal',
];

class AdHocOthersContainer extends Component {
  constructor(props) {
    super(props);

    const {
      isAdHoc,
      adHocData,
    } = this.props;

    const paramsWording = getCheckoutParamsWording(
      true,
      adHocData,
    );

    const headerCoverage = getCheckoutHeaderDataCoverage('Insurance', isAdHoc, adHocData);
    const sessionParamsToUse = adHocData.data.session_params;

    this.state = {
      headerData: headerCoverage.headerData,
      mainDetails: {
        title: 'Insurance',
        attributes: compactCheckoutAttributes(_.map(defaultTopNames, param => ({
          title: _.startCase(_.replace(param, '_', ' ')),
          description: sessionParamsToUse[param],
        }))),
      },
      subDetails: [{
        key: 'Detail',
        leftDetail: {
          title: 'Details',
          attributes: compactCheckoutAttributes(_.map(defaultBottomNames, param => ({
            title: _.startCase(_.replace(param, '_', ' ')),
            description: sessionParamsToUse[param],
          }))),
        },
      }],
      ...paramsWording,
    };
  }

  componentDidMount() {
    gaTag({
      page_title: 'Adhoc - Checkout',
      page_path: '/adhoc/checkout',
    });
  }

  render() {
    const {
      headerData,
      mainDetails,
      subDetails,
      wording,
      params,
    } = this.state;

    const {
      isLoggedIn,
      charged,
      isAdHoc,
      adHocData,
      sessionId,
    } = this.props;

    return (
      <CheckoutWrapper
        headerData={headerData}
        mainDetails={mainDetails}
        subDetails={subDetails}
        sessionId={sessionId}
        isLoggedIn={isLoggedIn}
        charged={charged}
        backToDetails={() => {}}
        wording={wording}
        isAdHoc={isAdHoc}
        adHocData={adHocData}
        params={params}
        category="adhoc"
        trackingTags={{
          payment: () => {
            gaEvent('Adhoc - CheckoutPage_ProceedPayment', {
              event_category: 'Adhoc',
              event_label: 'Adhoc - CheckoutPage_ProceedPayment',
            });
          },
          receipt: () => {
            gaTag({
              page_title: 'Adhoc - Receipt',
              page_path: '/adhoc/Receipt',
            });
          },
        }}
      />
    );
  }
}

AdHocOthersContainer.defaultProps = {
  charged: null,
  sessionId: null,
  adHocData: null,
  isAdHoc: false,
  isLoggedIn: false,
};

AdHocOthersContainer.propTypes = {
  charged: PropTypes.bool,
  sessionId: PropTypes.string,
  isLoggedIn: PropTypes.bool,
  adHocData: PropTypes.object,
  isAdHoc: PropTypes.bool,
};

const mapStateToProps = (state, ownProps) => ({
  sessionId: ownProps.sessionId,
});

export default connect(mapStateToProps)(AdHocOthersContainer);
