/**
 * AdHocCheckoutContainer.js
 * Used for Adhoc payments/ receipts
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import APILoader from 'components/Templates/APILoader/APILoader';
import TravelCheckoutContainer from '../Travel/TravelCheckoutContainer';
import MotorCheckoutContainer from '../Motor/MotorCheckoutContainer';
import PACheckoutContainer from '../PA/PACheckoutContainer';
import PetCheckoutContainer from '../Pet/PetCheckoutContainer';
import AdHocOthersContainer from './AdHocOthersContainer';
import ErrorPage from '../../components/Templates/ErrorPage/ErrorPage';

import { getAdhocTransaction } from '../../actions/insuranceActions';

class AdHocCheckoutContainer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: null,
      apiLoader: null,
      charged: false,
    };

    this.setCharged = this.setCharged.bind(this);
  }

  async componentDidMount() {
    const { match } = this.props;

    const data = await getAdhocTransaction(match.params.id);

    this.setState({
      data,
      apiLoader: this.APILoader,
      charged: data && data.status === 'paid',
    });
  }

  async setCharged(charged) {
    await new Promise(resolve => this.setState({ charged }, resolve));
  }

  renderCheckout() {
    const { data, apiLoader, charged: localCharged } = this.state;
    const { match, charged, userDetails } = this.props;

    if (!data) {
      return null;
    }
    const isLoggedIn = !_.isEmpty(userDetails);

    try {
      switch (data.data.session_params.category) {
        case 'Travel':
          return (
            <TravelCheckoutContainer
              adHocData={data}
              sessionId={match.params.id}
              isAdHoc
              charged={localCharged}
              isLoggedIn={isLoggedIn}
              handleAPI={apiLoader ? apiLoader.handleAPI : () => {}}
              setCharged={this.setCharged}
            />);
        case 'Pet':
          return (
            <PetCheckoutContainer
              adHocData={data}
              sessionId={match.params.id}
              isAdHoc
              charged={charged}
              isLoggedIn={isLoggedIn}
            />);
        case 'Motor':
          return (
            <MotorCheckoutContainer
              adHocData={data}
              sessionId={match.params.id}
              isAdHoc
              charged={charged}
              isLoggedIn={isLoggedIn}
            />);
        case 'Personal Accident':
          return (
            <PACheckoutContainer
              adHocData={data}
              sessionId={match.params.id}
              isAdHoc
              charged={charged}
              isLoggedIn={isLoggedIn}
            />);
        default:
          return (
            <AdHocOthersContainer
              adHocData={data}
              sessionId={match.params.id}
              isAdHoc
              charged={charged}
              isLoggedIn={isLoggedIn}
            />
          );
      }
    } catch (error) {
      return <ErrorPage />;
    }
  }

  render() {
    return (
      <Fragment>
        {this.renderCheckout()}
        <APILoader ref={(node) => { this.APILoader = node; }} />
      </Fragment>
    );
  }
}

AdHocCheckoutContainer.defaultProps = {
  charged: null,
  userDetails: null,
};

AdHocCheckoutContainer.propTypes = {
  match: PropTypes.object.isRequired,
  charged: PropTypes.bool,
  userDetails: PropTypes.object,
};

const mapStateToProps = state => ({
  charged: state.insurance.charged,
  userDetails: state.userDetails.data,
});

export default connect(mapStateToProps)(AdHocCheckoutContainer);
